import Social from './social';

const Contact = () => {
   return (
      <section className="contact">
         <h1 className="main-title">Contact</h1>
         <form className="contact-form">
            <label htmlFor="name">
               <span>Nombre</span>
               <input type="text" name="name" id="name" className="form-field" />
            </label>
            <label htmlFor="email">
               <span>E-mail</span>
               <input type="email" name="email" id="email" className="form-field" />
            </label>
            <label htmlFor="msg">
               <span>Mensaje</span>
               <textarea name="msg" id="msg" className="form-field"></textarea>
            </label>
            <input type="button" className="button" value="Enviar" />
         </form>
         <Social className="contact-social" />
      </section>
   );
}

export default Contact;


